// -----------------------------------------------------------------------------
// MediaPipe æ¨¡çµ„è¼‰å…¥
// -----------------------------------------------------------------------------
async function ensureTasksVisionLoaded() {
  if (window.FilesetResolver && window.ImageSegmenter) return true;
  const urls = [
    "https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@0.10.7/vision_bundle.mjs",
    "https://unpkg.com/@mediapipe/tasks-vision@0.10.7/vision_bundle.mjs",
  ];
  for (const url of urls) {
    try {
      const mod = await import(url);
      if (mod.FilesetResolver) window.FilesetResolver = mod.FilesetResolver;
      if (mod.ImageSegmenter) window.ImageSegmenter = mod.ImageSegmenter;
      if (mod.FaceDetector) window.FaceDetector = mod.FaceDetector;
      return true;
    } catch (e) {
      console.warn("[WARN] failed to import tasks-vision from", url, e);
    }
  }
  throw new Error("ç„¡æ³•è¼‰å…¥ @mediapipe/tasks-vision æ¨¡çµ„");
}

async function initTasks() {
  if (!wasmFileset) {
    wasmFileset = await FilesetResolver.forVisionTasks(CONFIG.WASM_BASE);
  }
  // ä¾ outCanvas æ¯”ä¾‹é¸æ“‡ä½¿ç”¨ landscape/square
  try {
    const rect = outCanvas.getBoundingClientRect();
    CONFIG.SEGMENTER.USE_LANDSCAPE_MODEL =
      (rect.width || 1) >= (rect.height || 1);
  } catch {}

  // ImageSegmenter
  const baseOptions = {
    modelAssetPath: CONFIG.SEGMENTER.USE_LANDSCAPE_MODEL
      ? CONFIG.MODELS.SELFIE_SEGMENTER_LANDSCAPE
      : CONFIG.MODELS.SELFIE_SEGMENTER_SQUARE,
    delegate: CONFIG.SEGMENTER.USE_GPU ? "GPU" : "CPU",
  };
  try {
    imageSegmenter = await ImageSegmenter.createFromOptions(wasmFileset, {
      baseOptions,
      runningMode: "VIDEO",
      outputCategoryMask: false,
      outputConfidenceMasks: true,
    });
  } catch (e) {
    console.warn("[Segmenter] GPU å¤±æ•—ï¼Œæ”¹ç”¨ CPU:", e);
    imageSegmenter = await ImageSegmenter.createFromOptions(wasmFileset, {
      baseOptions: { ...baseOptions, delegate: "CPU" },
      runningMode: "VIDEO",
      outputCategoryMask: false,
      outputConfidenceMasks: true,
    });
  }

  // FaceDetectorï¼ˆå¯é¸ï¼‰
  try {
    faceDetector = await FaceDetector.createFromOptions(wasmFileset, {
      baseOptions: {
        modelAssetPath: CONFIG.MODELS.FACE_DETECTOR,
        delegate: "GPU",
      },
      runningMode: "VIDEO",
      minDetectionConfidence: 0.6,
      minSuppressionThreshold: 0.3,
    });
  } catch (e) {
    console.warn("[FaceDetector] GPU å¤±æ•—ï¼Œæ”¹ç”¨ CPU:", e);
    faceDetector = await FaceDetector.createFromOptions(wasmFileset, {
      baseOptions: {
        modelAssetPath: CONFIG.MODELS.FACE_DETECTOR,
        delegate: "CPU",
      },
      runningMode: "VIDEO",
      minDetectionConfidence: 0.6,
      minSuppressionThreshold: 0.3,
    });
  }
}

// -----------------------------------------------------------------------------
// ç›¸æ©Ÿæ§åˆ¶
// -----------------------------------------------------------------------------
async function startCamera() {
  if (stream) {
    try {
      stream.getTracks().forEach((t) => t.stop());
    } catch {}
  }
  const constraints = {
    audio: false,
    video: {
      facingMode: currentFacingMode,
      width: { ideal: CONFIG.CAMERA.WIDTH },
      height: { ideal: CONFIG.CAMERA.HEIGHT },
      frameRate: { ideal: CONFIG.CAMERA.FRAME_RATE },
    },
  };
  stream = await navigator.mediaDevices.getUserMedia(constraints);
  videoEl.srcObject = stream;
  videoEl.setAttribute("playsinline", "");
  videoEl.muted = true;
  videoEl.autoplay = true;

  await new Promise((resolve) => {
    if (videoEl.readyState >= 1 && videoEl.videoWidth && videoEl.videoHeight)
      return resolve();
    videoEl.onloadedmetadata = () => resolve();
    setTimeout(resolve, 1500);
  });
  try {
    await videoEl.play();
  } catch {}

  syncCanvasSize();
}

export async function switchCamera() {
  currentFacingMode = currentFacingMode === "user" ? "environment" : "user";
  await startCamera();
}

// -----------------------------------------------------------------------------
// ç•«å¸ƒå°ºå¯¸åŒæ­¥
// -----------------------------------------------------------------------------
export function syncCanvasSize() {
  const stageEl = outCanvas.closest(".stage") || outCanvas.parentElement;
  const rect = stageEl.getBoundingClientRect();
  const w = Math.max(1, Math.round(rect.width));
  const h = Math.max(1, Math.round(rect.height));
  [bgCanvas, maskCanvas, outCanvas, videoDrawCanvas, compositeCanvas].forEach(
    (c) => {
      if (c.width !== w || c.height !== h) {
        c.width = w;
        c.height = h;
      }
      c.style.width = `${w}px`;
      c.style.height = `${h}px`;
    }
  );
  lastMaskSize = { w: outCanvas.width, h: outCanvas.height };
}

// -----------------------------------------------------------------------------
// èƒŒæ™¯ç¹ªè£½ï¼ˆå«ã€Œå½±ç‰‡ã€èƒŒæ™¯é¸é …ï¼‰
// -----------------------------------------------------------------------------
function renderBackgroundTo(ctx, w, h) {
  const bgType =
    (CONFIG.BACKGROUND && CONFIG.BACKGROUND.TYPE) ||
    (CONFIG.BACKGROUND_EFFECTS &&
      CONFIG.BACKGROUND_EFFECTS.DEFAULT_BACKGROUND) ||
    "original";
  const intensity =
    CONFIG.BACKGROUND && typeof CONFIG.BACKGROUND.INTENSITY === "number"
      ? CONFIG.BACKGROUND.INTENSITY
      : (CONFIG.BACKGROUND_EFFECTS &&
          CONFIG.BACKGROUND_EFFECTS.EFFECT_INTENSITY) ??
        0.5;

  ctx.clearRect(0, 0, w, h);
  animT += 0.016;

  if (bgType === "original") {
    // é€æ˜
    return;
  }
  if (bgType === "waves") {
    const grad = ctx.createLinearGradient(0, 0, w, h);
    grad.addColorStop(0, "#1a2332");
    grad.addColorStop(0.5, "#2d3a50");
    grad.addColorStop(1, "#0f1419");
    ctx.fillStyle = grad;
    ctx.fillRect(0, 0, w, h);
    ctx.strokeStyle = `rgba(100,200,255,${intensity * 0.6})`;
    ctx.lineWidth = 2;
    for (let i = 0; i < 8; i++) {
      ctx.beginPath();
      for (let x = 0; x < w; x += 5) {
        const off = i * 50;
        const y =
          h / 2 + Math.sin((x + off) * 0.012 + animT * 1.2) * 20 * intensity;
        if (x === 0) ctx.moveTo(x, y);
        else ctx.lineTo(x, y);
      }
      ctx.stroke();
    }
    return;
  }
  if (bgType === "frequency") {
    ctx.fillStyle = "#0a0a0a";
    ctx.fillRect(0, 0, w, h);
    const bands = 48;
    const bw = w / bands;
    for (let i = 0; i < bands; i++) {
      const x = i * bw;
      const bh =
        (Math.sin(animT * 2 + i * 0.2) * 0.5 + 0.5) * h * 0.5 * intensity;
      const hue = (animT * 30 + i * 10) % 360;
      ctx.fillStyle = `hsla(${hue},70%,60%,0.8)`;
      ctx.fillRect(x, h - bh, bw - 1, bh);
    }
    return;
  }
  if (bgType === "video" && bgVideoEl) {
    // ä»¥ cover æ–¹å¼é‹ªæ»¿
    const vw = bgVideoEl.videoWidth || 1;
    const vh = bgVideoEl.videoHeight || 1;
    if (bgVideoEl.readyState >= 2) {
      const scale = Math.max(w / vw, h / vh);
      const dw = Math.round(vw * scale);
      const dh = Math.round(vh * scale);
      const ox = Math.round((w - dw) / 2);
      const oy = Math.round((h - dh) / 2);
      try {
        if (bgVideoEl.paused) bgVideoEl.play().catch(() => {});
      } catch {}
      ctx.globalAlpha = Math.min(1, 0.3 + intensity * 0.7);
      ctx.drawImage(bgVideoEl, 0, 0, vw, vh, ox, oy, dw, dh);
      ctx.globalAlpha = 1;
    }
    return;
  }
  // æœˆç›¸å½±ç‰‡èƒŒæ™¯
  if (bgType === "moon_video" && bgMoonVideoEl) {
    try {
      // Debug removed - moon video working properly
      
      if (bgMoonVideoEl.readyState >= 2 && !bgMoonVideoEl.paused) {
        const vw = bgMoonVideoEl.videoWidth || bgMoonVideoEl.width || 1;
        const vh = bgMoonVideoEl.videoHeight || bgMoonVideoEl.height || 1;
        if (vw > 1 && vh > 1) {
          const scale = Math.max(w / vw, h / vh);
          const dw = Math.round(vw * scale);
          const dh = Math.round(vh * scale);
          const ox = Math.round((w - dw) / 2);
          const oy = Math.round((h - dh) / 2);

          ctx.globalAlpha = Math.min(1, 0.3 + intensity * 0.7);
          ctx.drawImage(bgMoonVideoEl, 0, 0, vw, vh, ox, oy, dw, dh);
          ctx.globalAlpha = 1;
          // Moon video rendered successfully
        } else {
          console.warn("ğŸŒ™ Moon video dimensions too small:", { vw, vh });
        }
      } else if (bgMoonVideoEl.paused) {
        // Moon video is paused, attempting to play
        bgMoonVideoEl.play().catch((e) => console.warn("æœˆç›¸å½±ç‰‡æ’­æ”¾å¤±æ•—:", e));
      } else {
        console.warn("ğŸŒ™ Moon video not ready:", { 
          readyState: bgMoonVideoEl.readyState, 
          paused: bgMoonVideoEl.paused 
        });
      }
    } catch (e) {
      console.warn("æœˆç›¸å½±ç‰‡æ¸²æŸ“éŒ¯èª¤:", e);
    }
    return;
  }
  
  // è‡ªå®šç¾©èƒŒæ™¯ + åœ“å½¢æœˆäº®åˆæˆ
  if (bgType === "custom_moon_bg" && customBackgroundImage && bgMoonVideoEl) {
    try {
      // ç¬¬ä¸€å±¤ï¼šè‡ªå®šç¾©èƒŒæ™¯åœ–ç‰‡
      if (customBackgroundImage) {
        const imgW = customBackgroundImage.naturalWidth || customBackgroundImage.width;
        const imgH = customBackgroundImage.naturalHeight || customBackgroundImage.height;
        if (imgW > 0 && imgH > 0) {
          // ä»¥ cover æ–¹å¼é‹ªæ»¿
          const scale = Math.max(w / imgW, h / imgH);
          const dw = Math.round(imgW * scale);
          const dh = Math.round(imgH * scale);
          const ox = Math.round((w - dw) / 2);
          const oy = Math.round((h - dh) / 2);
          ctx.drawImage(customBackgroundImage, 0, 0, imgW, imgH, ox, oy, dw, dh);
        }
      }
      
      // ç¬¬äºŒå±¤ï¼šåœ“å½¢æœˆäº® MP4 (éŸ¿æ‡‰å¼åº§æ¨™è¨ˆç®—)
      if (bgMoonVideoEl.readyState >= 2 && !bgMoonVideoEl.paused) {
        const vw = bgMoonVideoEl.videoWidth || bgMoonVideoEl.width || 1;
        const vh = bgMoonVideoEl.videoHeight || bgMoonVideoEl.height || 1;
        
        if (vw > 1 && vh > 1) {
          ctx.save();
          
          // éŸ¿æ‡‰å¼åœ“å½¢è£å‰ªè·¯å¾‘è¨ˆç®—
          // æ ¹æ“šè¢å¹•æ¯”ä¾‹èª¿æ•´æœˆäº®ä½ç½®ï¼Œç¢ºä¿åœ¨å„ç¨®æ‰‹æ©Ÿå°ºå¯¸ä¸‹éƒ½èƒ½æ­£ç¢ºé¡¯ç¤º
          const aspectRatio = w / h;
          let centerX, centerY, radius;
          
          if (aspectRatio > 0.6) {
            // è¼ƒå¯¬çš„è¢å¹• (å¦‚å¹³æ¿æ©«å‘)
            centerX = w * 0.5;
            centerY = h * 0.35;
            radius = Math.min(w, h) * 0.25;
          } else if (aspectRatio > 0.5) {
            // æ¨™æº–æ‰‹æ©Ÿè¢å¹• (9:16 åˆ° 9:18)
            centerX = w * 0.51;
            centerY = h * 0.29;
            radius = Math.min(w, h) * 0.29;
          } else {
            // è¶…é•·è¢å¹• (å¦‚ 9:19.5, 9:20)
            centerX = w * 0.51;
            centerY = h * 0.26;
            radius = Math.min(w, h) * 0.30;
          }
          
          ctx.beginPath();
          ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
          ctx.clip();
          
          // ç¹ªè£½æœˆäº®å½±ç‰‡ (ä»¥ cover æ–¹å¼)
          const scale = Math.max(w / vw, h / vh);
          const dw = Math.round(vw * scale);
          const dh = Math.round(vh * scale);
          const ox = Math.round((w - dw) / 2);
          const oy = Math.round((h - dh) / 2);
          
          ctx.globalAlpha = Math.min(1, 0.8 + intensity * 0.2);
          ctx.drawImage(bgMoonVideoEl, 0, 0, vw, vh, ox, oy, dw, dh);
          ctx.globalAlpha = 1;
          
          ctx.restore();
        }
      } else if (bgMoonVideoEl.paused) {
        // è‡ªå‹•æ’­æ”¾æœˆç›¸å½±ç‰‡
        bgMoonVideoEl.play().catch((e) => console.warn("æœˆç›¸å½±ç‰‡æ’­æ”¾å¤±æ•—:", e));
      }
      
    } catch (e) {
      console.warn("è‡ªå®šç¾©èƒŒæ™¯ + åœ“å½¢æœˆäº®æ¸²æŸ“éŒ¯èª¤:", e);
    }
    return;
  }
}

